window.onload = function() { document.documentElement.style.opacity = '1'; }

const swiper = new Swiper('.swiper-container', {
  // Optional parameters
  direction: 'horizontal',
  loop: true,
  slidesPerView: 3,
  slideToClickedSlide: true,
  centeredslides: true,
  height: 500,
  spaceBetween: 0,

  // If we need pagination
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
    draggable: true,
  },

  autoplay: {
    delay: 5000,
  },

  // Navigation arrows
  navigation: {
    nextEl: '.swiper-button-next',
    prevEl: '.swiper-button-prev',
  },

  breakpoints: {
    320: {
      slidesPerView: 1
    },
    992: {
      slidesPerView: 2
    },
    1799: {
      slidesPerView: 3
    }
  },

  mousewheel: {
    sensitivity: 1,
    eventsTarget: ".swiper-slide",
  }

});

// Fixed Header 

const header = document.querySelector('.header');
const first = document.querySelector('.main__slider');
const headerHeight = header.offsetHeight;
const firstHeight = first.offsetHeight;
let lastScrollTop = 0;

window.addEventListener('scroll', () => {
	let scrollDistance = window.scrollY;

	if (scrollDistance >= firstHeight + headerHeight) {
		header.classList.add('header--fixed');
		first.style.marginTop = `${headerHeight}px`;
	} else {
		header.classList.remove('header--fixed');
		first.style.marginTop = null;
	}

});

// Header Mobile 
let body = document.body;
let headerMobileBtn = document.querySelector('.header__mobile-btn');
let headerMobileMenu = document.querySelector('.header__mobile-menu');
let headerMobileClose = document.querySelector('.header__mobile-close');

headerMobileBtn.addEventListener('click', function () {
  body.classList.add('no-scroll');
  headerMobileMenu.classList.add('header__mobile-menu--opened');
  header.classList.add('header__mobile-menu--active');
});

headerMobileClose.addEventListener('click', function() {
  body.classList.remove('no-scroll');
  headerMobileMenu.classList.remove('header__mobile-menu--opened');
  header.classList.remove('header__mobile-menu--active');
});

// Main Navigation

let mainMobileBtn = document.querySelector('.main__mobile-btn');
let mainMobileMenu = document.querySelector('.main__mobile-menu');
let mainMobileClose = document.querySelector('.main__mobile-close');

mainMobileBtn.addEventListener('click', function () {
  body.classList.add('no-scroll');
  mainMobileMenu.classList.add('main__mobile-menu--opened');
});

mainMobileClose.addEventListener('click', function() {
  body.classList.remove('no-scroll');
  mainMobileMenu.classList.remove('main__mobile-menu--opened');
});